export default{
	data(){
		return {
			petList: [],
			api: this.$API
		}
	},
	onLoad(){
		this.getPetList();
	},
	methods:{
		// 获取用户宠物列表
		getPetList() {
			this.$http.get('/petbnb/getPetList')
				.then(res => {
					if (res.code === 1) {
						this.petList = res.data.list;
					}
				})
				.catch(err => {
		 
				})
		},
	}
}
